@import url('https://fonts.googleapis.com/css2?family=Fira+Sans:wght@700&family=Secular+One&display=swap');

.button {
  height: 5rem;
  width: 10rem;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-direction: row;
  border-radius: 0.2rem;
  border: 0;
  background: var(--button-colort);
}

.tel {
  height: 2rem;
  width: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0.5rem;
}

.link {
  text-decoration: none;
  color: var(--font-color1);
  display: flex;
  align-items: center;
  justify-content: center;
  font: 400 1.1rem "Secular One", sans-serif;

}

:root {
  --button-colort: #42e787;
}