@import url('https://fonts.googleapis.com/css2?family=Fira+Sans:wght@700&family=Poppins&display=swap');


* {
  margin: 0;
  padding: 0;
}

#container {
  width: 100%;
  background: rgb(210, 228, 231);

}

.initial {
  display: flex;
  justify-content: space-between;
  height: 4rem;

  display: flex;
  align-items: center;
  padding: 1rem;
  background: var(--header-color);

}

.nav {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 15rem;
  animation: rec 0.8 steps(60);
}

.nav_button {
  display: flex;
  align-items: center;
  border: 0;
  background: rgba(255, 255, 255, 0);
}

.navigation {
  display: none;
}


.main {
  text-align: center;
}


.onest {
  margin: 4px var(--shadow-color);
  padding-top: 5rem;

  background: white;
}


.titulo {
  padding: 1.5rem;
  color: var(--button-color);
  font: 700 var(--font-size-fira) "Fira Sans", sans-serif;
}

.doctor1 {
  width: 17rem;
  height: 18rem;
  position: relative;
  border-radius: 0.5rem;
}


.imgbox::before {
  content: "";
  height: 18rem;
  width: 17rem;
  background: var(--shadow-color);
  position: absolute;
  top: 10rem;
  left: 0.5rem;
  border-radius: 0.5rem;
  z-index: 0;
}

.text {
  text-align: center;
  font: 400 var(--font-size-secular) "Poppins", italic;
  text-align: center;
  padding-bottom: 2rem;

}

.secaccordion {
  display: none;
}


.bottom {
  width: 100%;
  height: 2px;
  background: var(--shadow-color);
}

.options {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 1rem 0;
}

.doctors {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}




.parts_of_the_list {
  display: flex;
  flex-direction: row;
  padding: 1rem 1rem;
  text-align: center;
}

.img_doctor {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 2rem;
}

.name {
  border: 5px solid var(--button5-hover);
  border-radius: 2.16rem;
}

.doctor_writes {
  margin-left: 1rem;
  font: 400 var(--font-size-secular) "Poppins", sans-serif;
  border-radius: 0.8rem;
  background: white;

}

.lab {
  display: none;
}

.footer {

  height: 20rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: var(--button-colort);
  padding-top: 2rem;
  font: 400 var(--font-size-secular) "Poppins", sans-serif;

}

.map iframe {
  height: 30rem;
  width: 100%;
  margin: 0;
  margin-top: 1rem;
  border: 0;

}

.map {
  height: 30rem;

  margin: 0;
}

.link0 {
  color: black;
  text-decoration: none;
}



/* //////////MEDIA/////////// */


@media (min-width: 1200px) {


  .nav:hover {
    animation: nav 0.5s steps(60);
  }

  .second {
    display: none;

  }

  .doctor1 {
    width: 24rem;
    height: 25rem;
  }

  .textaling {
    margin-right: 0.4rem;
  }

  .imgbox::before {
    content: "";
    height: 25rem;
    width: 25rem;
    background: var(--shadow-color);
    position: absolute;
    top: 10rem;
    left: 0.5rem;
    border-radius: 0.5rem;
    z-index: 0;
  }

  .box_acc {
    display: flex;
    flex-direction: row;
    margin: 0;
  }

  .button_title {
    display: none;
  }



  .secaccordion {
    display: block;
  }

  .options {
    background: none;
  }

  .parts_of_the_list {
    width: 35rem;
  }
}


@media (min-width: 992px) {
  .navigation {
    display: flex;
  }

  .nav {
    display: flex;
    margin-right: 1rem;
    text-decoration: none;
    font: 400 var(--font-size-secular-one) "Poppins", sans-serif;
    color: black;
  }

  .titulo {
    font: 700 var(--font-size-fira-sans) "Fira Sans", sans-serif;
    display: flex;
    text-align: center;

  }

  .text {
    font: 400 var(--font-size-secular-one) "Poppins", sans-serif;
    text-align: center;
  }

  .onest {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

  }

  .textaling {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .doctor1 {
    margin-left: 1rem;
  }

  .imgbox::before {
    margin-left: 1rem;

  }

  .doctor1,
  .desk .textaling {
    display: flex;
    justify-content: space-between;
  }

  .box_acc {
    display: flex;
    flex-direction: row;

    justify-content: space-between;
    width: 100%;
  }

  .desk {
    display: flex;
    flex-direction: column;
    width: 40rem;
    text-align: center;
  }


  .second {
    margin-bottom: 1rem;
    padding: 1.5rem;
    color: var(--button-color);
    font: 700 var(--font-size-fira) "Poppins", sans-serif;
  }

  .doctors {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .listen {
    width: 25rem;
  }


  .lab {
    display: flex;
    width: 35rem;
    height: auto;
    border-radius: 2rem;
    margin-right: 1rem;
  }

  .footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .big_logo {
    display: flex;
    align-items: center;

  }

  .part_footer {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-right: 5rem;
  }

  :root {
    --font-size-secular-one: 1.25rem;
    --font-size-fira-sans: 2.25rem;
  }
}




@keyframes nav {
  from {
    opacity: 0;

  }

  to {
    opacity: 1;
  }
}

@keyframes rec {
  0% {
    transform: translateX(80px);
  }

  100% {
    transform: translateX(0px);
  }
}

:root {
  --button0-hover: #9e6743;
  --button1-hover: #bf7065;
  --button2-hover: #deee4c;
  --button3-hover: #d42c20;
  --button4-hover: #0a73dc;
  --button5-hover: #1BE020;
  --container-color: #96FBC0;
  --header-color: #4CFA94;
  --shadow-color: #497A5E;
  --font-color1: #ffff;

  --font-size-secular: 1rem;
  --font-size-fira: 2rem;

}