@import url('https://fonts.googleapis.com/css2?family=Fira+Sans:wght@700&family=Secular+One&display=swap');

.accordion {
  width: 100%;

}

.accordion p {
  overflow: hidden;
  font: 400 1rem "Secular One", sans-serif;
  text-align: center;
  border-radius: 0.5rem;
  animation: go 1s;
  padding: 1rem;
  background: white;
  border-radius: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;


}





.acc {
  margin: 2rem 2rem;

  height: 8rem;
  width: 8rem;
  border-radius: 2rem;
  border: 0;
  background: var(--button-colort);
}

.icons {
  height: 6rem;
  width: 6rem;
  animation: emerge 1s;

}

@media (min-width: 1200px) {
  .secaccordion {
    width: auto;
    display: flex;
    align-items: center;
    justify-content: center;

  }

  .secaccordion:hover .after {
    display: none;
  }



  .secaccordion p {
    font: 400 1rem "Poppins", sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    animation: emerge 1s;

  }

  .accordion {
    display: none;
  }



  .secacc {
    width: 20rem;
    height: 20rem;
    margin: 1rem;
    border-radius: 0;
    border: 0;
    border-radius: 2rem;
    transition: 0.5s;

  }



  .secacc:hover {
    background: var(--button5-hover);
    text-align: center;

  }

  .box_acc {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 0;
  }



  /*.zero button:hover {
    background: var(--button0-hover);

  }

  .one button:hover {
    background: var(--button1-hover);
  }

  .two button:hover {
    background: var(--button2-hover);
  }

  .three button:hover {
    background: var(--button3-hover);
  }

  .four button:hover {
    background: var(--button4-hover);
  }

  .five button:hover {
    background: var(--button5-hover);
  }*/


  .acc:hover {
    cursor: pointer;
    background: var(--button2-hover);
    transition: 0.6s;
  }


}


@media (min-width: 992px) {

  .accordiond .acc {
    margin: 0 1rem;
    display: grid;

  }

  .box_acc .acc_position,
  .box_acc .acc_repair {
    display: flex;
    flex-direction: row;
  }

  .accordion p {
    width: 17rem;
    margin-left: 1.5rem;
    animation: go 1s;

  }



}

@keyframes go {
  0% {
    transform: translateY(2rem);
  }

  100% {
    transform: translateY(0);
  }

}

@keyframes emerge {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

:root {
  --button-color: #257A48;
  --container-color: #96FBC0;
  --header-color: #4CFA94;
  --shadow-color: #497A5E;
  --button-colort: #42e787;
  --font-color1: #ffff;
  --button0-hover: #9e6743;
  --button1-hover: #93443a;
  --button2-hover: #dbee32;
  --button3-hover: #d42c20;
  --button4-hover: #0a73dc;
  --button5-hover: #1BE020;


  --font-size-secular: 1rem;
  --font-size-fira: 2rem;

}